import utils from '../wxs/utils.js';
export default {
	name: 'youInfo',
	props: {
		customStyle: {
			type: String,
			default: ''
		},
		dot: {
			type: Boolean,
			default: false
		}
	},
	computed: {
		info: function() {
			let dot = this.dot;
			let info = utils.bem('info', { dot });
			return info
		}
	}
}
